import React from 'react';
import { Button, Popover } from 'antd';

const contentComp = (desc) => (
  <div>
    <p>{desc}</p>
  </div>
);

const Pill = ({title, desc}) => (
  <Popover content={contentComp(desc)} title={title}>
    <Button type="primary" style={{width: 200}}>{title}</Button>
  </Popover>
);

export default Pill;